body {
  background-color: #f5fdee;
  font-family: Arial, sans-serif;
}

.frame-chatroom {
  background-color: #fff;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  height: 100vh;
  display: flex;
}

.header {
  background-color: #baec99;
  justify-content: center;
  align-items: center;
  height: 74px;
  padding: 10px;
  display: flex;
  position: relative;
}

.group-chat-name {
  color: #000;
  text-align: center;
  background-color: #0000;
  border: 0;
  height: 35px;
  font-family: Lexend Mega, Helvetica;
  font-size: 28px;
  font-weight: 700;
  transform: rotate(-.15deg);
}

.icon-person-outline, .icon-arrow-ios-back {
  width: 30px;
  height: 30px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.icon-arrow-ios-back {
  width: 20px;
  left: 24px;
}

.icon-person-outline {
  right: 24px;
}

.chat-body {
  flex-direction: column;
  flex: 1;
  display: flex;
}

.messages-container {
  background-color: #f5fdee;
  flex: 1;
  padding: 10px;
  overflow-y: auto;
}

.message-wrapper {
  flex-direction: column;
  margin: 5px 0;
  display: flex;
}

.message-bubble {
  color: #fff;
  border-radius: 10px;
  max-width: 60%;
  margin-bottom: 5px;
  padding: 10px;
  font-family: Lexend Zetta, Helvetica;
  font-size: 19px;
  font-weight: 600;
}

.message-sender {
  color: #555;
  font-size: .8em;
}

.current-user .message-bubble {
  color: #fff;
  background-color: #046603;
  align-self: flex-end;
}

.other-user .message-bubble {
  color: #000;
  background-color: #dcdcdc;
  align-self: flex-start;
}

.send-text {
  background-color: #baec99;
  padding: 10px;
}

.input-group {
  align-items: center;
  display: flex;
}

.type-here {
  border: none;
  border-radius: 20px;
  flex: 1;
  margin-right: 10px;
  padding: 10px;
  font-family: Lexend Zetta, Helvetica;
  font-size: 18px;
  font-weight: 500;
}

.send-button {
  color: #fff;
  cursor: pointer;
  background-color: #046603;
  border: none;
  border-radius: 20px;
  align-items: center;
  padding: 10px 20px;
  font-family: Lexend Zetta, Helvetica;
  font-size: 14px;
  font-weight: 700;
  display: flex;
}

@media (width <= 1200px) {
  .group-chat-name {
    font-size: 24px;
  }

  .icon-person-outline, .icon-arrow-ios-back {
    width: 25px;
    height: 25px;
  }

  .type-here {
    font-size: 16px;
  }

  .send-button {
    padding: 8px 16px;
    font-size: 12px;
  }
}

@media (width <= 992px) {
  .group-chat-name {
    font-size: 20px;
  }

  .icon-person-outline, .icon-arrow-ios-back {
    width: 20px;
    height: 20px;
  }

  .type-here {
    font-size: 14px;
  }

  .send-button {
    padding: 6px 12px;
    font-size: 10px;
  }
}

@media (width <= 768px) {
  .group-chat-name {
    font-size: 18px;
  }

  .icon-person-outline, .icon-arrow-ios-back {
    width: 18px;
    height: 18px;
  }

  .type-here {
    font-size: 12px;
  }

  .send-button {
    padding: 4px 10px;
    font-size: 8px;
  }
}

@media (width <= 576px) {
  .header {
    flex-direction: column;
    align-items: center;
  }

  .group-chat-name {
    width: 100%;
    font-size: 16px;
  }

  .icon-person-outline, .icon-arrow-ios-back {
    width: 16px;
    height: 16px;
  }

  .type-here {
    font-size: 10px;
  }

  .send-button {
    padding: 2px 8px;
    font-size: 6px;
  }
}

@media (width <= 768px) {
  .header {
    padding: 0 10px;
  }

  .group-chat-name {
    font-size: 20px;
  }

  .icon-arrow-ios-back, .icon-person-outline {
    width: 30px;
    height: 30px;
  }

  .message-bubble {
    max-width: 80%;
  }

  .input-group {
    flex-direction: column;
    padding: 5px;
  }

  .type-here {
    margin-bottom: 10px;
    font-size: 14px;
  }

  .send-button {
    width: 100%;
    padding: 10px;
  }
}

